<template>
	<section>
		
		<div class="inner">
	        <div class="inr-que">
				<div class="list-search clearfix">
	              <input type="text" class="sea-srk" placeholder="请输入油种" v-model="oli">
	              <button class="btn-sea" type="button" @click="searchFn()">搜索</button>
	              <div class="block">
  				<span class="demonstration">月</span>
  <el-date-picker
  	class='times'
    v-model="value4"
    type="month"
    placeholder="选择月">
  </el-date-picker>
</div>

	            </div>
		        <table>
			        <tr class="inrq-t">
				        <th>原油</th>
				        <th>轻质石脑油(%)</th>
				        <th>中质石脑油(%)</th>
				        <th>航煤(%)</th>
				        <th>瓦斯油(%)</th>
				        <th>减压瓦斯油(%)</th>
				        <th>重质减压油(%)</th>
				        <th>减压渣油(%)</th>
				        <th>校对(%)</th>
			        </tr>
			        <tr v-for="item in infoArr">
	        			<td class="inrq-bg">{{item.crudeNameE}}</td>
	        			<td class="inrq-bg">{{item.lightNaptha}}</td>
	        			<td class="inrq-bg">{{item.mediumNaptha}}</td>
	        			<td class="inrq-bg">{{item.aviationFuel}}</td>
	        			<td class="inrq-bg">{{item.gasOil}}</td>
	        			<td class="inrq-bg">{{item.vacuumGasOil}}</td>
	        			<td class="inrq-bg">{{item.heavyFeliefOil}}</td>
	        			<td class="inrq-bg">{{item.vacuumResidue}}</td>
	        			<td class="inrq-bg">{{item.check}}</td>
	        		</tr>	
		        </table>   
	       	<!--分页-->
	       	<div style="text-align: center; margin-top: 30px;">
	       		<el-pagination
			     @current-change='pageFn'
				 layout="prev, pager, next"
				 :total="total">
				</el-pagination>
       		</div>  
	    	</div>    
		</div>
	</section>
</template>

<script>
	import { liuFen }   from '../../api/info';
	export default{
		data(){
			return{
				value4:'',
				total:0,
				pageNum:1,
				pageSize:10,
				infoArr:[],
				oli:''
			}
		},
		methods:{
			liufenFn(){
				var param ={
					pageNum:this.pageNum,
					pageSize:this.pageSize,
				}
				liuFen(param).then(res =>{
					this.total = res.total
					this.infoArr = res.datas
				})
			},
			searchFn(){
				console.log(this.oli)
				var params={
					pageNum:this.pageNum,
					pageSize:this.pageSize,
					crudeNameE:this.oli
				}
				liuFen(params).then(res =>{
					this.total = res.total
					this.infoArr = res.datas
				})
			},
			pageFn(sty){
				console.log(sty)
				this.pageNum = sty
				var param = {
					pageNum:sty,
					pageSize:this.pageSize,
					crudeNameE:this.oli
				}
				liuFen(param).then(res =>{
					this.total = res.total
					this.infoArr = res.datas
				})
			}
		},
		created(){
			
		},
		mounted(){
			this.liufenFn()
		}
	}
</script>

<style>
	/*.inr-que p>span {
	    width: 28%;
	    padding-left: 4%;
	    border-right: none;
	    border-radius: 4px 0 0 4px;
	}
	.inr-que p>input{
        font-size: 14px;
	    float: left;
	    height: 32px;
	    line-height: 32px;
	    border: 1px solid #e3e3e3
	}*/
	.list-search{width:100%;margin-bottom:30px;}
	.sea-srk{float:left;width:40%;height:38px;background:#fff;line-height:50px;border:0; border-radius:4px 0 0 4px;padding-left:20px;font-size:18px;color:#2c3e50;border: 1px solid #C0C0C0;}
	.btn-sea{float:left;width:10%;height:40px;background:#66a6fe;border:0;border-radius:0px 4px 4px 0;color:#fff;font-size:18px;}
	.current{
		
	   
	}
	.times{
		position: relative;
	}
</style>


